* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #222;
  margin: 0;
  padding: 0;
  font-family: arial;
  font-size: 75%;
  position: relative;
  overflow-y: hidden;
}

canvas {
  z-index: 1;
  border: 1px solid #181818;
  display: block;
  box-shadow: 0 0 10px #111;
}

.nav {
  z-index: 2;
  text-align: center;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
}

.nav a {
  color: #f0f021;
  margin: 0 15px;
  font-size: 15px;
  font-weight: 300;
  text-decoration: none;
}

[data-link] {
  color: #006eff;
  text-transform: uppercase;
  letter-spacing: .02em;
  padding: 8px 20px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
}

.cursor {
  pointer-events: none;
  z-index: 1;
  width: 30px;
  height: 30px;
  pointer-events: none;
  border: 2px solid #f80;
  margin: -15px 0 0 -15px;
  transition: all .2s ease-out;
  position: fixed;
  top: 0;
  left: 0;
}

.cursor .cursor-ball {
  width: 4px;
  height: 4px;
  background: #ffb300;
  border-radius: 4px;
  margin: -2px 0 0 -2px;
  transition: all .4s ease-out;
  position: absolute;
  top: 50%;
  left: 50%;
}

.cursor.hover {
  border-radius: 8px;
}

.cursor.hover .cursor-ball {
  left: 13px;
}

.nav {
  justify-content: center;
  padding-top: 35px;
  display: flex;
}

@media (max-width: 480px) {
  .nav {
    flex-direction: column;
    align-items: center;
  }

  .nav a {
    margin: 10px 0;
  }

  [data-link] {
    padding: 5px 10px;
  }

  .cursor {
    width: 20px;
    height: 20px;
  }
}

